import React from 'react'
import BorderedButton from './child-component/button'

export default function Footer() {
    return (
        <footer id='contact'>
                <div className='flex flex-col justify-center items-center px-6 mb-10 md:px-32'>
                    <p className='text-2xl font-bold text-secondary mb-4'>Get In Touch</p>
                    <p className='text-base font-normal text-text text-center mb-8'>Although I’m not currently looking for any new opportunities, my inbox is always open. Whether you have a question or just want to say hi, I’ll try my best to get back to you!</p>
                    <BorderedButton name="Send me a message"/>
                </div>
                <a href="https://github.com/riskiidn"><p className='text-base font-bold text-secondary left-0 right-0 text-center py-6'>Designed and built by Riski Dwi Patrio</p></a>
            </footer>
    )
}
