import React, {useState} from 'react'
import { faBars } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

export default function Navbar() {

    const [toggleNav, settoggleNav] = useState(false)

    return (
        <header className='relative'>
            <div className='hidden px-10 py-6 h-14 bg-gray-900 w-full md:flex md:justify-between md:items-center'>
                <a href="/" className='text-xl font-bold text-secondary'>Portofolio</a>
                <nav className='flex w-1/2 justify-evenly items-center text-xs text-secondary font-bold'>
                    <a href="#about">About</a>
                    <a href="#education">Education</a>
                    <a href="#work">Work</a>
                    <a href="#contact">Contact</a>
                    <a href='../cv.pdf' className='border-2 border-secondary px-6 rounded-xl text-xs py-1 '>Resume</a>
                </nav>
            </div>
            <div className='md:hidden w-full bg-blue-900 bg-opacity-20 py-4 px-6 text-lg flex justify-between items-center'>
                <div onClick={()=>settoggleNav(!toggleNav)} className='text-secondary text-2xl'>
                    <FontAwesomeIcon icon={faBars} />
                </div>
                <p className='font-bold text-2xl text-secondary'>Portofolio</p>
            </div>
            <div className={`md:hidden transition-transform ${toggleNav ? 'transform translate-x-0 py-8 px-4 ' : 'transform -translate-x-full h-0 '} bg-blue-900 bg-opacity-20 text-secondary rounded-b-xl`}>
                <div className='mb-4 text-2xl font-bold '><a className='' href="/">Portofolio</a></div>
                <nav className='flex flex-col text-lg font-semibold mb-4'>
                    <a className='mb-2' href="#about">About</a>
                    <a className='mb-2' href="#education">Education</a>
                    <a className='mb-2' href="#work">Work</a>
                    <a href="#contact">Contact</a>
                </nav>
                <a href='../cv.pdf' className='px-8 py-2 rounded-xl border-secondary border-2 text-center text-secondary text-lg'>Resume</a>
            </div>
        </header>
    )
}
