
import React from 'react'
import EduComp from './component/child-component/eduComp'
import ProjectComp from './component/child-component/projectComp'
import Footer from './component/Footer'
import Navbar from './component/Navbar'

export default function Home() {

    return (
        <div id='content' className='h-screen w-screen overflow-y-auto overflow-x-hidden text-text'>
            <Navbar />
            <main className='md:px-24'>
                <section id='welcome' className='h-screen px-4 mt-16    '>
                    <div className=' md:w-4/5'>
                        <p className='text-sm text-secondary font-normal mb-6'>Hi, my name is</p>
                        <p className='text-3xl md:text-5xl md:font-extrabold font-bold text-text mb-3'>Riski Dwi Patrio.</p>
                        <p className='text-3xl md:text-5xl md:font-extrabold font-bold text-gray-400 mb-6'>Web and Mobile Apps Developper.</p>
                        <p className='text-lg md:text-sm font-normal text-gray-400 mb-10'>I am a web developer, who specializes in front end development and can also develop backend.</p>
                        <a href='../cv.pdf' className='border-2 border-secondary max-w-max p-4 rounded-xl font-bold text-sm text-secondary'>Download My Resume</a>
                    </div>
                </section>
                <section id='about' className='mb-20'>
                    <div className='px-4'>
                        <div className='mb-8 md:mb-6 md:flex md:items-start'>
                            <p className='mb-4 text-2xl text-secondary font-bold md:border-b-2 md:border-secondary md:pb-2'>About Me</p>
                            <hr className='border-secondary md:hidden' />
                        </div>
                        <div className='flex flex-col justify-center md:flex-row md:items-start items-center'>
                            <div className='w-full md:flex-1 md:pr-10 mb-10 md:text-sm'>
                                <p>Fresh graduate in the field of informatics engineering, have a curiosity on the development of information technology. I can use several programming languages ​​and have a passion for learning new programming technology.
                                    Have hope and interest to continue to learn and have a career as a programmer who reliable and with integrity as a form of my contribution to improving the quality of human resources.

                                    Here are a few programming language I’ve been working with recently:
                                    <ul className='mt-4'>
                                        <li>PHP</li>
                                        <li>JavaScript (ES6+)</li>
                                        <li>React</li>
                                        <li>Dart</li>
                                    </ul>
                                </p>
                            </div>
                            <div className='w-56 h-56 border-secondary border-2 rounded-xl bg-foto bg-cover'>
                            </div>

                        </div>
                    </div>
                </section>
                <EduComp/>
                <section id='work' className='mb-20'>
                    <div className='project p-4'>
                        <div className='mb-8'>
                            <p className='mb-4 text-2xl text-secondary md:border-b-2 md:max-w-max md:pb-2 md:border-secondary   font-bold'>Something I've Built</p>
                            <hr className='border-secondary md:hidden' />
                        </div>
                        <div className=''>
                            <ProjectComp title="Environment Monitoring and Early Warning System" year="ongoing" instance="Lembaga Ilmu Pengetahuan Indonesia (LIPI)" desc="Aplikasi mengakses data dan informasi  lingkungan perairan, termasuk peringatan dini kematian massal ikan." tools={['VSCode', 'Laravel', 'ReactJs', 'MySQL']} img="bg-olm" link="http://olm.scientistidn.com/" />

                            <ProjectComp title="Android Mobile Apps “D-Invite”
                                        D-Invinte" year="2021" instance="Skripsi IBI Darmajaya" desc="Aplikasi pembuatan undangan digital berbasis android.  with payment, messenger, invitation maker feature." tools={['VSCode', 'Laravel', 'Flutter', 'Firebase']} img="bg-diinvite" link="/" />

                            <ProjectComp title="ULICOSTE”
                                        D-Invinte" year="2020" instance="Lembaga Pengabdian dan Pemberdayaan Masyarakat
                                        (LPPM) Universitas Lampung." desc="Website informasi penyelenggaraan konfrensi internasional Universitas Lampung." tools={['VSCode', 'PHP', 'Bootstrap']} img="bg-ulicoste" link="https://ulicoste.unila.ac.id/2020/" />

                            <ProjectComp title="Open Jorunal System Website" year="2021" instance="Research and Social Study Institute" desc="Webiste publishing dan manejemen jurnal penelitian Sains Terbuka." tools={['VSCode', 'OJS', 'Smarty', 'Bootstrap', 'MySQL']} img="bg-voctech" link="https://voctech.net/index.php/journal" />
                        </div>
                    </div>
                </section>
            </main>
            <Footer />
        </div>
    )
}
