import React from 'react'
import { faExternalLinkAlt, faLink } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

export default function ProjectComp(props) {

    function  goTo() {
        window.location.href = props.link
    }

    return (
        <div className='md:flex md:flex-row-reverse md:items-center md:mb-6 md:border-2 md:shadow-xl md:border-overlay rounded-xl  p-6'>
            <div onClick={()=>goTo()} className={`${props.img} bg-cover bg-center md:bg-none md:w-3/5 rounded-xl`}>
                <div className={`md:pl-6 p-6 mb-6 bg-overlay bg-opacity-80 md:bg-primary md:opacity-100 md:flex rounded-xl md:justify-end md:flex-col md:items-end`}>
                    <div className='mb-6 md:text-right'>
                        <p className='font-bold mb-2 text-white text-lg'>{props.title}</p>
                        <p className='text-sm font-normal'>{props.year}</p>
                    </div>
                    <div className='mb-6 md:text-right md:bg-overlay md:p-6 md:shadow-xl md:rounded-xl'>
                        <p className='mb-2 text-base text-secondary'>{props.instance}</p>
                        <p>{props.desc}</p>
                    </div>
                    <ul className='flex md:w-full text-secondary font-bold text-base justify-evenly items-center'>
                        {props.tools.map((tool) => (
                            <li>{tool}</li>
                        ))}
                    </ul>
                    <div className='md:flex md:justify-center md:items-center md:w-full md:mt-4 hidden'>
                        <a href={props.link} className='md:text-secondary hover:text-white md:cursor-pointer'><FontAwesomeIcon icon={faExternalLinkAlt} /></a>
                    </div>
                </div>
            </div>
            <div className='md:bg-overlay md:opacity-100 md:w-2/5 md:max-h-52 md:rounded-lg hover:opacity-50'>
                <a href={props.link}>
                    <div className={`md:h-52 md:${props.img} md:bg-center md:bg-cover md:rounded-lg md:block hidden`}>
                    </div>
                </a>
            </div>
        </div>
    )
}
